@mixin text ($fontFamily, $font-color, $font-size, $line-height ){
  font-family: $fontFamily;
  color:$font-color;
  font-size:$font-size;
  line-height:$line-height;
}

@mixin backgroundImage (){
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

